.day {
  position: relative;
  outline: var(--calendar-spacing) solid hsl(50, 100%, 0%); /* use instead of border */
  margin-top: var(--calendar-spacing);
  margin-left: var(--calendar-spacing);
  /* border-right: 0.15rem solid rgb(0, 0, 0);
  border-bottom: 0.15rem solid rgb(0, 0, 0); */
  width: 7rem;
  height: 7rem;
  display: flex;
  justify-content: center;
  border-radius: 1rem;
}

